.popup__inputs {
  margin-bottom: 0;
  outline: 0;
  border-style: none;
  box-shadow: none;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 10px;
  padding-left: 0;
  width: 238px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}

@media (min-width: 550px) {
  .popup__inputs {
    width: 358px;
  }
}